<template>
  <div class="AssessmentPrint">
    <div v-if="month">
      <div v-for="user in userItems" :key="user.snkey">
        <div class="titleZone d-flex justify-space-between">
          <h3>{{cData.company_name}}</h3>
          <h1 class="text-center border-bottom my-2">
            <strong>全人評估表</strong>
          </h1>
          <div class="mr-2 align-self-end text-right font-weight-bold">
            姓名: {{ user.name }} <br> 床號: {{ user.bed_name}} 病歷號: {{ user.p_code }}
          </div>
        </div>

        <v-divider></v-divider>
        <!-- 13張表單內容 -->
        <div class="mx-1">
          <!-- 健康習慣 -->
          <div class="form">
            <h3 class="grey lighten-3">健康習慣評估表</h3>
            <hr>
            <HealthyHabits :list="list" v-for="(list,index) in matchData('healthyhabits',user.snkey)" :key="index" />
          </div>

          <!-- 疾病史評估表 -->
          <div class="form">
            <h3 class="grey lighten-3">疾病史評估表</h3>
            <hr>
            <MedicalHistories :list="list" v-for="(list,index) in matchData('medicalhistories',user.snkey)" :key="index"  />
          </div>
          
          <!-- 藥物安全性評估表 -->
          <div class="form">
            <h3 class="grey lighten-3">藥物安全性評估表</h3>
            <hr>
            <DrugSafeties :list="list" v-for="(list,index) in matchData('drugsafeties',user.snkey)" :key="index"  />
          </div>

          <!-- 身體評估評估表 -->
          <div class="form">
            <h3 class="grey lighten-3">身體評估評估表</h3>
            <hr>
            <BodyEvaluations :list="list" v-for="(list,index) in matchData('bodyevaluations',user.snkey)" :key="index"  />
          </div>

          <!-- 壓力性損傷危險評估 -->
          <div class="form">
            <h3 class="grey lighten-3">壓力性損傷危險評估</h3>
            <hr>
            <PressureInjuries :list="list" v-for="(list,index) in matchData('pressureinjuries',user.snkey)" :key="index"  />
          </div>

          <!-- 跌倒危險性評估 -->
          <div class="form">
            <h3 class="grey lighten-3">跌倒危險性評估</h3>
            <hr>
            <FallRisks :list="list" v-for="(list,index) in matchData('fallrisks',user.snkey)" :key="index"  />
          </div>

          <!-- 日常生活功能評估 -->
          <div class="form">
            <h3 class="grey lighten-3">日常生活功能評估</h3>
            <hr>
            <ADLs :list="list" v-for="(list,index) in matchData('adls',user.snkey)" :key="index"  />
          </div>

          <!-- 工具性日常生活活動功能評估 -->
          <div class="form">
            <h3 class="grey lighten-3">工具性日常生活活動功能評估</h3>
            <hr>
            <IADLs :list="list" v-for="(list,index) in matchData('iadls',user.snkey)" :key="index"  />
          </div>

          <!-- 認知功能評估 -->
          <div class="form">
            <h3 class="grey lighten-3">認知功能評估</h3>
            <hr>
            <Dementias :list="list" v-for="(list,index) in matchData('dementias',user.snkey)" :key="index"  />
          </div>

          <!-- 情緒問題評估 -->
          <div class="form">
            <h3 class="grey lighten-3">情緒問題評估</h3>
            <hr>
            <GeriatricDepressionScales :list="list" v-for="(list,index) in matchData('geriatricdepressionscales',user.snkey)" :key="index"  />
            <div class="text-right mt-2">
              <span class="mr-6">社工: ______________________</span>
            </div>
          </div>

          <!-- 簡易營養評估 -->
          <div class="form">
            <h3 class="grey lighten-3">簡易營養評估</h3>
            <hr>
            <MNASFs :list="list" v-for="(list,index) in matchData('mnasfs',user.snkey)" :key="index"  />
            <div class="text-right mt-2">
              <span class="mr-6">營養師: ______________________</span>
            </div>
          </div>

          <!-- 疼痛評估 -->
          <div class="form">
            <h3 class="grey lighten-3">疼痛評估</h3>
            <hr>
            <PainEvaluations :list="list" v-for="(list,index) in matchData('painevaluations',user.snkey)" :key="index"  />
          </div>

          <!-- 衰弱評估 -->
          <div class="form">
            <h3 class="grey lighten-3">衰弱評估</h3>
            <hr>
            <SOFs :list="list" v-for="(list,index) in matchData('sofs',user.snkey)" :key="index"  />
          </div>

        </div>
        <!-- 13張表單內容end -->
        <div class="text-right mt-2">
          <span class="mr-6">護理師: ______________________</span>
        </div>
<div>
  <p style="page-break-after: always"></p>
  <hr>
</div>
      </div>
    </div>
  </div>
</template>

<script>
import HealthyHabits from "@/views/document/mohw/HealthyHabits/Form"
import MedicalHistories from "@/views/document/mohw/MedicalHistories/Form"
import DrugSafeties from "@/views/document/mohw/DrugSafeties/Form"
import BodyEvaluations from "@/views/document/mohw/BodyEvaluations/Form"
import PressureInjuries from "@/views/document/mohw/PressureInjuries/Form"
import FallRisks from "@/views/document/mohw/FallRisks/Form"
import ADLs from "@/views/document/mohw/ADLs/Form"
import IADLs from "@/views/document/mohw/IADLs/Form"
import Dementias from "@/views/document/mohw/Dementias/Form"
import GeriatricDepressionScales from "@/views/document/mohw/GeriatricDepressionScales/Form"
import MNASFs from "@/views/document/mohw/MNASFs/Form"
import PainEvaluations from "@/views/document/mohw/PainEvaluations/Form"
import SOFs from "@/views/document/mohw/SOFs/Form"

export default {
  components:{ 
    HealthyHabits, 
    MedicalHistories, 
    DrugSafeties, 
    BodyEvaluations, 
    PressureInjuries, 
    FallRisks, 
    ADLs,
    IADLs,
    Dementias,
    GeriatricDepressionScales,
    MNASFs,
    PainEvaluations,
    SOFs
    },
  data(){
    return {
      cData:"",
      month: "",
      userItems:null,
      formDatas:{}, //拿來存放讀進來各表格的資料
      formNames:[
        'healthyhabits',
        'medicalhistories',
        'drugsafeties',
        'bodyevaluations',
        'pressureinjuries',
        'fallrisks',
        'adls',
        'iadls',
        'dementias',
        'geriatricdepressionscales',
        'mnasfs',
        'painevaluations',
        'sofs'
      ], //各表格資料庫名稱
    }
  },

  mounted(){
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    let DATAs = JSON.parse(this.$route.query.data);
    this.month = DATAs.month;
    this.userItems = DATAs.userItems;

    this.formNames.forEach(i=>{
      this.getItems(i)
    })
  },

  methods:{
    // 找出符合資料 1.在指定月份裡 2.指定病歷snkey
    matchData(database,userSnkey){
      if (this.formDatas[database]){
        return this.formDatas[database].filter(i=>i.user_snkey == userSnkey && i.Date.includes(this.month))
      }
    },
    //讀入各表單資料
    async getItems(database){
      await this.$api.get(database)
      .then(rs=>{
        let data = rs.map(i=>{
          return {
            ...JSON.parse(i.datalist),
            snkey : i.snkey,
          }
        })

        this.$set(this.formDatas,database,data)
      })
    }
  }
}
</script>

<style scoped>
.form{
  border:1px black solid;
  margin-top:5px;
}
</style>